@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500;700;900&display=swap);
* {
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

.App {
  display: -webkit-flex;
  display: flex;
  background-color: #22252d;
  height: 100vh;
}
.overview {
  width: 100%;
  margin: 30px;
  background-color: #2e3139;
  border-radius: 15px;
}

.navbar {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-width: 200px;
  background-color: #22252d;
  color: rgb(255, 255, 255);
}
.navbar h2 {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  padding: 40px 30px;
}
.nav-links {
  list-style: none;
  padding: 0;
  margin-top: 5vh;
}
.nav-links li button a {
  font-family: "Poppins", sans-serif;
  text-decoration: none;
  color: rgb(255, 255, 255);
}
.btn {
  width: 170px;
  height: 50px;
  border-radius: 9px;
  background: #485563; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #29323c,
    #485563
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.btn i {
  margin: 0 5px;
}

.district {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 5vh 10px 0 10px;
}
.district > div h2 {
  font-family: "Lato", sans-serif;
  color: rgb(245, 237, 237);
  margin-left: 30px;
}
.selectors {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
select {
  -webkit-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0 !important;
  background: #5c6664;
  background-image: none;
  -webkit-flex: 1 1;
          flex: 1 1;
  padding: 0 0.5em;
  color: #fff;
  cursor: pointer;
  font-size: 1em;
  font-family: "lato", sans-serif;
}
select::-ms-expand {
  display: none;
}
.select {
  position: relative;
  display: -webkit-flex;
  display: flex;
  width: 20em;
  height: 3em;
  line-height: 3;
  background: #5c6664;
  overflow: hidden;
  border-radius: 0.25em;
  margin: 0 20px;
}
.select::after {
  content: "\25BC";
  position: absolute;
  top: 0;
  right: 0;
  padding: 0 1em;
  background: #2b2e2e;
  color: #ffff;
  cursor: pointer;
  pointer-events: none;
  transition: 0.25s all ease;
}
.select:hover::after {
  color: #23b499;
}

.cards {
  margin: 5vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.card {
  height: 130px;
  width: 250px;
  text-align: center;
  padding-top: 30px;
  margin: 15px;
  border-radius: 5px;
  background-color: #22252d;
  color: #fff;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
.card h4 {
  display: inline;
}
.card div:nth-child(2) {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.icon {
  width: 45px;
  height: 45px;
  border-radius: 5px;
  margin-left: 10px;
  background-color: aqua;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.icon i {
  font-size: 30px;
}
.cards div:nth-child(1) .icon {
  background-color: rgb(255, 7, 58);
}
.cards div:nth-child(2) .icon {
  background-color: rgb(40, 167, 69);
}
.cards div:nth-child(3) .icon {
  background-color: rgb(108, 117, 125);
}

.chart {
  width: 37vw;
  background-color: #22252d;
  margin-left: 30px;
  border-radius: 8px;
  padding: 10px;
}

